___CSS_LOADER_EXPORT___.push([module.id, `#currentWeather {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.25);
  padding: 1rem;
  border-radius: 1rem;
  color: whitesmoke;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.25));
}

#currLocation {
  font-size: 3rem;
  color: var(--primary-yellow);
}

#currTemperature {
  font-size: 0rem;
  color: white;
}

#currCondition {
  font-size: 0rem;
  color: var(--primary-gray);
}

#currHighLow {
  display: flex;
  gap: 1rem;
  font-size: 0rem;
  color: white;
}

@media (max-width: 600px) {
  #currentWeather {
    margin-top: 8rem;
  }
}
