async function getWeatherData(location, unit) {
  try {
    const response = await fetch(
      `https://weather.visualcrossing.com/VisualCrossingWebServices/rest/services/timeline/${location}/?key=A8CR7YBPYFD93BMV55SA6U7TP&unitGroup=${unit}`,
      { mode: "cors" }
    );
    const data = await response.json();
    return data;
  } catch (error) {
    console.error(error);
  }
}
export async function extractCurrentWeather(location, unit) {
  try {
    const data = await getWeatherData(location, unit);
    const commas = data.resolvedAddress.split(",");
    const locationStr =
      commas.length >= 2 ? commas.slice(0, 2).join(",") : data.resolvedAddress;

    const currentWeather = {
      location: locationStr,
      condition: data.currentConditions.conditions,
      actualTemp:
        Math.round((data.currentConditions.temp + 0.1) * 10) / 10 + "°",
      feelsTemp: data.currentConditions.feelslike + "°",
      high: "↑ " + data.days[0].tempmax + "°",
      low: "↓ " + data.days[0].tempmin + "°",
    };

    return currentWeather;
  } catch (error) {
    console.error(error);
  }
}

export async function extractWeeklyWeather(location, unit) {
  try {
    const data = await getWeatherData(location, unit);

    const weeklyWeather = data.days.slice(1, 8).map((day) => {
      const processedDay = {
        condition: day.conditions,
        temp: day.temp,
        high: day.tempmax,
        low: day.tempmin,
        icon: day.icon,
        date: day.datetime,
      };
      return processedDay;
    });

    return weeklyWeather;
  } catch (error) {
    console.error(error);
  }
}
