import { extractCurrentWeather, extractWeeklyWeather } from "./data.js";
const icons = require.context("../icons", false, /\.svg$/);

export async function currentWeatherDOM(location, unit) {
  try {
    const currentWeather = await extractCurrentWeather(location, unit);

    // Update location
    const currLocation = document.getElementById("currLocation");
    currLocation.textContent = currentWeather.location;

    // Update actual temperature
    const currTemperature = document.getElementById("currTemperature");
    currTemperature.textContent = currentWeather.actualTemp;

    // Update condition
    const currCondition = document.getElementById("currCondition");
    currCondition.textContent = currentWeather.condition;

    // Update high temperature
    const currHigh = document.getElementById("currHigh");
    currHigh.textContent = currentWeather.high;

    // Update high temperature
    const currLow = document.getElementById("currLow");
    currLow.textContent = currentWeather.low;
  } catch (error) {
    console.error("Error in currentWeatherDOM:", error);
  }
}

export function equalizeTextWidth() {
  try {
    const currentWeatherElements =
      document.getElementById("currentWeather").children;

    for (let i = 0; i < 4; i++) {
      const currEle = currentWeatherElements[i];
      currEle.style.fontSize = `0px`;
      let fontSize = 0;

      while (currEle.offsetWidth < 160) {
        fontSize += 1;
        currEle.style.fontSize = `${fontSize}px`;
      }
    }
  } catch (error) {
    console.error("Error in equalizeTextWidth:", error);
  }
}

function getDayAbbreviation(date) {
  const days = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];
  return days[date.getDay()];
}

export async function weeklyWeatherDOM(location, unit) {
  try {
    const weeklyWeatherContainer = document.getElementById("weeklyWeather");
    weeklyWeatherContainer.innerHTML = "";

    const weeklyWeather = await extractWeeklyWeather(location, unit);

    for (const weekday in weeklyWeather) {
      const day = weeklyWeather[weekday];

      const container = document.createElement("div");
      container.className = "weekdayWeather";
      weeklyWeatherContainer.appendChild(container);

      const name = document.createElement("span");
      const date = new Date(day.date);
      name.textContent = getDayAbbreviation(date);
      name.className = "weekday";
      container.appendChild(name);

      const temp = document.createElement("span");
      temp.textContent = day.temp + "°";
      temp.className = "weekdayTemperature";
      container.appendChild(temp);

      const icon = document.createElement("img");
      icon.src = icons(`./${day.icon}.svg`);
      icon.className = "weekdayIcon";
      container.appendChild(icon);

      const condition = document.createElement("span");
      condition.textContent = day.condition;
      condition.className = "weekdayCondition";
      container.appendChild(condition);

      const highLow = document.createElement("div");
      highLow.className = "weekdayHighLow";
      container.appendChild(highLow);

      const high = document.createElement("span");
      high.textContent = "↑ " + day.high + "°";
      high.className = "weekdayHigh";
      highLow.appendChild(high);

      const low = document.createElement("span");
      low.textContent = "↑ " + day.low + "°";
      low.className = "weekdayLow";
      highLow.appendChild(low);
    }
  } catch (error) {
    console.error("Error in weeklyWeatherDOM:", error);
  }
}
