import "../styles/main.css";
import "../styles/current.css";
import "../styles/header.css";
import "../styles/weekly.css";
import "../styles/toggle.css";
import feather from "feather-icons";
import { currentWeatherDOM, weeklyWeatherDOM, equalizeTextWidth } from "./dom";

feather.replace();

if (!localStorage.getItem("unit")) {
  localStorage.setItem("unit", "us");
}
if (!localStorage.getItem("toggleChecked")) {
  localStorage.setItem("toggleChecked", "false");
}

const toggleInput = document.querySelector("#toggleInput");
const toggleCircle = document.querySelector("#toggleCircle");

toggleInput.checked = localStorage.getItem("toggleChecked") === "true";
toggleCircle.textContent = toggleInput.checked ? "°C" : "°F";

toggleInput.addEventListener("change", function () {
  if (toggleInput.checked) {
    toggleCircle.textContent = "°C";
    localStorage.setItem("unit", "metric");
    localStorage.setItem("toggleChecked", "true");
  } else {
    toggleCircle.textContent = "°F";
    localStorage.setItem("unit", "us");
    localStorage.setItem("toggleChecked", "false");
  }
});

const unit = localStorage.getItem("unit");
await currentWeatherDOM("New York", unit);
await weeklyWeatherDOM("New York", unit);
equalizeTextWidth();

document
  .querySelector("form")
  .addEventListener("submit", async function (event) {
    event.preventDefault();

    const location = document.getElementById("searchInput").value;
    const unit = localStorage.getItem("unit");

    await currentWeatherDOM(location, unit);
    await weeklyWeatherDOM(location, unit);
    equalizeTextWidth();
  });
