___CSS_LOADER_EXPORT___.push([module.id, `header {
  width: 100vw;
  margin-bottom: 2rem;
  background: linear-gradient(to bottom, rgba(92, 92, 92, 0.462), transparent);
  height: 2.5rem;
}

#headerContent {
  padding: 0.75rem;
  display: flex;
  justify-content: space-between;
}

#logo {
  padding: 0;
  margin: 0;
  height: min-content;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  color: var(--primary-yellow);
}

#logoName {
  font-size: 2rem;
  font-weight: 500;
}

#logoIcon {
  width: 2rem;
  height: auto;
  stroke-width: 2.5;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.25));
}

#searchInput {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 1rem;
  opacity: 0.75;
  font-size: 1rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s ease, opacity 0.3s ease;
}

#searchInput:focus {
  outline: none;
  opacity: 1;
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.5), 0 1rem 2rem rgba(0, 0, 0, 0.2);
}

#headerRight {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

@media (max-width: 600px) {
  #headerContent {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
}
