___CSS_LOADER_EXPORT___.push([module.id, `#toggleLabel {
  width: 4rem;
  height: 2rem;
  display: block;
  background-color: white;
  border-radius: 100rem;
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(0, 0, 0, 0.2);
}

#toggleInput {
  display: none;
}

#toggleCircle {
  position: absolute;
  left: 0rem;
  width: 1.5rem;
  height: 1.5rem;
  background-color: var(--primary-yellow);
  color: black;
  border-radius: 100rem;
  margin: 0.25rem;
  transition: 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 500;
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(0, 0, 0, 0.2);
}

#toggleInput:checked + #toggleCircle {
  left: 2rem;
  background-color: #145ca6;
  color: white;
}

@media (max-width: 600px) {
  #toggleContainer {
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
}

@media (max-width: 400px) {
  #toggleContainer {
    position: relative;
    top: auto;
    right: auto;
    margin: 1rem auto;
    transform: none;
  }
}

@media (max-width: 300px) {
  #toggleContainer {
    margin: 0.5rem auto;
  }
}
