___CSS_LOADER_EXPORT___.push([module.id, `.forecastTitle {
  margin-top: 5rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 500;
  color: white;
}

#weeklyWeather {
  max-width: 100vw;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 8rem;
  gap: 0.5rem;
  overflow-x: scroll;
  scrollbar-color: rgba(255, 255, 255, 0.509) transparent;
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.25));
}

.weekdayWeather {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem;
  color: whitesmoke;
  transition: 0.3s linear;
  padding: 0.5rem;
  gap: 0.25rem;
}

.weekday {
  font-size: 2rem;
  color: var(--secondary-yellow);
}

.weekdayTemperature {
  font-size: 2rem;
}

.weekdayIcon {
  width: 3rem;
  height: 3rem;
}

.weekdayCondition {
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
  color: var(--primary-gray);
  text-align: center;
}

.weekdayHighLow {
  display: flex;
  justify-content: space-between;
  width: max-content;
  gap: 0.5rem;
  font-size: 1rem;
}

@media (max-width: 600px) {
  .forecastTitle {
    margin-top: 8rem;
    margin-bottom: 0.5rem;
    font-size: 1.2rem;
  }

  #weeklyWeather {
    grid-auto-columns: 6rem;
    gap: 0.25rem;
  }

  .weekdayWeather {
    padding: 0.25rem;
    gap: 0.125rem;
  }

  .weekday {
    font-size: 1.5rem;
  }

  .weekdayTemperature {
    font-size: 1.5rem;
  }

  .weekdayIcon {
    width: 2rem;
    height: 2rem;
  }

  .weekdayCondition,
  .weekdayHighLow {
    font-size: 0.8rem;
  }
}
